import { __ } from '@wordpress/i18n';
import { Button, ButtonGroup, BaseControl, Dropdown, ColorPicker, ColorPalette, Dashicon, } from '@wordpress/components';
import './editor.lazy.scss';


const DualColorPicker = ({label, colorSwitcher, normalColor, hoverColor, onColorSwitcherChange, onNormalColorChange, onHoverColorChange }) => {

    const NORMAL_HOVER = [
        { label: __('Normal', 'post-grid-block'), value: 'normal' },
        { label: __('Hover', 'post-grid-block'), value: 'hover' },
    ];

    const handleColorSwitcherChange = (value) => {
        onColorSwitcherChange(value);
    };

    const handleNormalColorChange = (value) => {
        onNormalColorChange(value);
    };

    const handleHoverColorChange = (value) => {
        onHoverColorChange(value);
    };

    return (
        <div className="global-setting-color-container">
            <BaseControl
                label={__(label, "post-grid-block")}
                className="global-setting-color-panel-wrapper"
            >
                <ButtonGroup className="global-setting-color-panel-btn">
                    {NORMAL_HOVER.map((item, key) => (
                        <Button
                            key={key}
                            onClick={() => handleColorSwitcherChange(item.value)}
                            className={`global-setting-color-panel-btn-switcher ${colorSwitcher === item.value ? 'active' : ''}`}
                        >
                            {item.label}
                        </Button>
                    ))}
                </ButtonGroup>
            </BaseControl>

            {colorSwitcher === 'normal' && (
                <BaseControl 
                    label={__("Normal Color", "post-grid-block")}
                    className="global-setting-color-switcher-wrapper"
                >
                    <Dropdown
                        className="global-setting-color-panel-icon-dropdown"
                        contentClassName="global-setting-color-panel-icon-dropdown-content"
                        popoverProps={{ placement: 'bottom-start' }}
                        renderToggle={({ isOpen, onToggle }) => (
                            <Button
                                variant="primary"
                                onClick={onToggle}
                                aria-expanded={isOpen}
                                className="global-setting-color-panel-icon-btn"
                            >
                                <Dashicon icon="color-picker" className="global-setting-color-panel-icon" />
                            </Button>
                        )}
                        renderContent={() => (
                            <div className="global-setting-color-panel-icon-popup">
                                <BaseControl className="global-setting-color-panel-icon-popup-inner">
                                    <div className="global-setting-normal-color">
                                        <ColorPicker
                                            color={normalColor}
                                            onChange={handleNormalColorChange}
                                            enableAlpha={true}
                                        />
                                        <div className="global-setting-color-label">{__("Default Colors", "post-grid-block")}</div>
                                        <ColorPalette
                                            colors={[
                                                { color: '#F72585' },
                                                { color: '#7209B7' },
                                                { color: '#3A0CA3' },
                                                { color: '#4361EE' },
                                                { color: '#4CC9F0' },
                                            ]}
                                            disableCustomColors={true}
                                            value={normalColor}
                                            onChange={handleNormalColorChange}
                                        />
                                    </div>
                                </BaseControl>
                            </div>
                        )}
                    />
                </BaseControl>
            )}

            {colorSwitcher === 'hover' && (
                <BaseControl 
                    label={__("Hover Color", "post-grid-block")}
                    className="global-setting-color-switcher-wrapper"
                >
                    <Dropdown
                        className="global-setting-color-panel-icon-dropdown"
                        contentClassName="global-setting-color-panel-icon-dropdown-content"
                        popoverProps={{ placement: 'bottom-start' }}
                        renderToggle={({ isOpen, onToggle }) => (
                            <Button
                                variant="primary"
                                onClick={onToggle}
                                aria-expanded={isOpen}
                                className="global-setting-color-panel-icon-btn"
                            >
                                <Dashicon icon="color-picker" className="global-setting-color-panel-icon" />
                            </Button>
                        )}
                        renderContent={() => (
                            <div className="global-setting-color-panel-icon-popup">
                                <BaseControl className="global-setting-color-panel-icon-popup-inner">
                                    <div className="global-setting-hover-color">
                                        <ColorPicker
                                            color={hoverColor}
                                            onChange={handleHoverColorChange}
                                            enableAlpha={true}
                                        />
                                        <div className="global-setting-hover-color-label">{__("Default Colors", "post-grid-block")}</div>
                                        <ColorPalette
                                            colors={[
                                                { color: '#F72585' },
                                                { color: '#7209B7' },
                                                { color: '#3A0CA3' },
                                                { color: '#4361EE' },
                                                { color: '#4CC9F0' },
                                            ]}
                                            disableCustomColors={true}
                                            value={hoverColor}
                                            onChange={handleHoverColorChange}
                                        />
                                    </div>
                                </BaseControl>
                            </div>
                        )}
                    />
                </BaseControl>
            )}
        </div>
    );
};

export default DualColorPicker;
