import { __ } from '@wordpress/i18n';

import { 
    InspectorControls, 
} from '@wordpress/block-editor';

import { 
    SelectControl,
    PanelBody,
    TabPanel,
    TextControl,
    RangeControl,
} from '@wordpress/components';

/**
 * Internal depencencies
 */
import {
    BORDER_STYLES,
} from "@Constants";

import { useEffect } from "@wordpress/element";
import ResponsiveSpacingControl from '@Components/responsive-spacing-control';
import DualColorPicker  from '@Components/color-control/dual-colorpicker';
import ResponsiveRangeControl from '@Components/responsive-range-control';
import BoxShadowSettingsControl from '@Components/box-shadow-control';
import './editor.scss';

export default function Inspector ({ attributes, setAttributes,  clientId}) {
    const {
        blockId,
        deviceType,

    } = attributes;

    useEffect(() => {
        if (!blockId) {
            setAttributes({
                blockId: "post-grid-block-" + clientId.slice(0, 8),
            });
        }
    }, [blockId, clientId, setAttributes]);

    
    return (
        <>

            <InspectorControls key="controls">
                <div className="post-grid-block-inspector-control">
                    <TabPanel
                        className="post-grid-block-parent-tab-panel"
                        activeClass="active-tab"
                        tabs={[
                            {
                                name: "general",
                                title: "General",
                                className: "post-grid-block-tab general",
                            },
                            {
                                name: "styles",
                                title: "Style",
                                className: "post-grid-block-tab styles",
                            },
                            {
                                name: "advanced",
                                title: "Advanced",
                                className: "post-grid-block-tab advanced",
                            },
                        ]}
                    >
                        {(tab) => (
                            <div className={"post-grid-block-tab-controls" + tab.name}>
                                {tab.name === 'general' && (
                                    <>
                                        <PanelBody >
                                            General
                                        </PanelBody>
                                    </>
                                )}

                                {tab.name === "styles" && (
                                    <>
                                        <PanelBody initialOpen={true}>
                                            Styles
                                        </PanelBody>
                                    </>
                                )}
                                {tab.name === "advanced" && (
                                    <>
                                        Advanced
                                    </>
                                )}
                            </div>
                        )}
                    </TabPanel>
                </div>
            </InspectorControls>
            </>
    );
}