import { useEffect } from 'react';
import { __ } from '@wordpress/i18n';
import { Button, ButtonGroup, RangeControl } from '@wordpress/components';
import ResponsiveDeviceControl from '@Components/responsive-device-control';
import './editor.lazy.scss';

const ResponsiveRangeControl = ({ min = 0, max = 100, label, value, onChange, onUnitChange, deviceType, units, setAttributes, step = 1, defaultValue = ''}) => {

    const UNIT_OPTIONS = [
		{ label: 'px', value: 'px' },
		{ label: '%', value: '%' },
		{ label: 'em', value: 'em' },
	];

    useEffect(() => {
        if (value[deviceType] === undefined) {
            const newValue = { ...value, [deviceType]: defaultValue };
            onChange(newValue);
        }
    }, [deviceType, value, onChange, defaultValue]);

    const handleValueChange = (newValue) => {
        const updatedValue = { ...value, [deviceType]: newValue || defaultValue };
        onChange(updatedValue);
    };

    const handleUnitChange = (unit) => {
        const newUnits = { ...units, [deviceType]: unit };
        onUnitChange(newUnits);
    };

    return (
        <div className="global-setting-spacing-container">
            <div className="global-setting-spacing-inner-container">
                <div className="global-setting-spacing-label-container">
                    <ResponsiveDeviceControl
						label={ __( label, 'post-grid-block' )}
						selectedDevice={ deviceType }
						onDeviceChange={( newDevice ) => setAttributes({ deviceType: newDevice })}
					/>
                    <div className="global-setting-units-type">
                        <ButtonGroup className="global-setting-units-type-wrap">
                            {UNIT_OPTIONS.map(({ label: unitLabel, value: unitValue }) => (
                                <Button
                                    key={unitValue}
                                    onClick={() => handleUnitChange(unitValue)}
                                    className={`global-setting-units-type-switcher ${units[deviceType] === unitValue ? 'active' : ''}`}
                                >
                                    {unitLabel}
                                </Button>
                            ))}
                        </ButtonGroup>
                    </div>
                </div>
                <RangeControl
                    value={value[deviceType]}
                    onChange={handleValueChange}
                    min={min}
                    max={max}
                    step={step}
                    allowReset
                    resetFallbackValue={defaultValue}
                />
            </div>
        </div>
    );
};

export default ResponsiveRangeControl;
