
import React from 'react';
import {__} from '@wordpress/i18n';
import {
	Button,
	ButtonGroup
} from '@wordpress/components';
import ResponsiveDeviceControl from '@Components/responsive-device-control';
import './editor.lazy.scss';

export default function ResponsiveSpacingControl({ label, deviceType, unitsHandler, units, desktopSpacing, tabletSpacing, mobileSpacing, onUnitChange, isLinked, onLinkToggle, setAttributes }) {

	const handleSpacingUnit = ( event, position ) => {
		onUnitChange( event, position );
	}

	const handleLinkToggle = () => {
		onLinkToggle()
	};

	const UNIT_TYPES = [
		{ label: 'px', value: 'px' },
		{ label: '%', value: '%' },
		{ label: 'em', value: 'em' },
	];

	// Get spacing value
	const getSpacingValue = ( position ) => {
		if ( deviceType === 'Desktop' ) {
			return desktopSpacing[position];
		} else if ( deviceType === 'Tablet' ) {
			return tabletSpacing[position];
		}

		return mobileSpacing[position];
	};

	const setSpacingUnit = (unit) => {
		unitsHandler(unit);
	}

	return (
		<div className="global-setting-spacing-container">
			<div className="global-setting-spacing-inner-container">
				<div className="global-setting-spacing-label-container">
					<ResponsiveDeviceControl
                        label={__(label, 'post-grid-block')}
                        selectedDevice={deviceType}
                        onDeviceChange={(newDevice) => setAttributes({ deviceType: newDevice })}
                    />
					<div className="global-setting-units-type">
						<ButtonGroup className="global-setting-units-type-wrap">
							{UNIT_TYPES.map( ( item, key ) => (
								<Button
									key={key}
									onClick={() => setSpacingUnit(item.value)}
									className={`global-setting-units-type-switcher ${units[deviceType] === item.value ? 'active' : ''}`}
								>
									{item.label}
								</Button>
							) )}
						</ButtonGroup>
					</div>
				</div>
				<div className="global-setting-spacing-input-container">
					<div className="global-setting-spacing-input-wrapper">
						<label className="global-setting-spacing-input-label">{__( 'Top', 'post-grid-block' )}</label>
						<input
							type="number"
							name="top"
							value={getSpacingValue( 'top' )}
							onChange={( e ) => handleSpacingUnit( e, 'top' )}
						/>
					</div>
					<div className="global-setting-spacing-input-wrapper">
						<label className="global-setting-spacing-input-label">{__( 'Right', 'post-grid-block' )}</label>
						<input
							type="number"
							name="right"
							value={getSpacingValue( 'right' )}
							onChange={( e ) => handleSpacingUnit( e, 'right' )}
						/>
					</div>
					<div className="global-setting-spacing-input-wrapper">
						<label className="global-setting-spacing-input-label">{__( 'Bottom', 'post-grid-block' )}</label>
						<input
							type="number"
							name="bottom"
							value={getSpacingValue( 'bottom' )}
							onChange={( e ) => handleSpacingUnit( e, 'bottom' )}
						/>
					</div>
					<div className="global-setting-spacing-input-wrapper">
						<label className="global-setting-spacing-input-label">{__( 'Left', 'post-grid-block' )}</label>
						<input
							type="number"
							name="left"
							value={getSpacingValue( 'left' )}
							onChange={( e ) => handleSpacingUnit( e, 'left' )}
						/>
					</div>
					<div className="global-setting-spacing-input-wrapper">
						<label className="global-setting-spacing-input-label"></label>
						<span
							className={`dashicons ${isLinked ? 'dashicons-admin-links' : 'dashicons-editor-unlink'} ${isLinked ? 'is-checked' : ''}`}
							onClick={handleLinkToggle}
						>
						</span>
					</div>
				</div>
			</div>
		</div>
	)
}
